.slider {
  position: relative;
  background: $white;
  &__header {
    position: sticky;
    top: 0;
    z-index: 6;
    width: 100%;
    height: 100px;
    background: $white;
    transition: all .5s linear 0s;
    .container, .row {
      height: 100%; }
    &__block {
      border: 1px solid #000;
      height: 100%;
      @include center;
      img {
        height: 60px; } } }
  &__content {
    padding-top: 150px;
    position: relative;
    @include r(767) {
      padding-top: 0; }
    &__pagination {
      position: sticky;
      padding-left: 20px;
      display: block;
      span {
        display: block;
        width: 15px;
        height: 15px;
        border-radius: 100%;
        background: rgba(0, 0, 0, 0.3);
        margin: 5px 0;
        &.active {
          background: rgba(0, 0, 0, 1); } } } }
  &__img {
    position: sticky;
    right: 0;
    width: 100%;
    img {
      width: 100%;
      position: relative;
      right: -2rem; } }
  &__slide {
    margin-top: -200px;
    height: 100vh;
    padding-left: 50px;
    @include r(767) {
      padding-left: 0;
      height: auto;
      margin-top: 0; }
    &__img {
      height: 100px;
      @include center;
      img {
        height: 100%; } }
    &:last-child {
      height: auto; }
    h2 {
      padding: 100px 0;
      @include r(767) {
        text-align: center; } } } }
